import React from "react";
import "./Hero.scss";
import {HeroItem} from './HeroItem';

const Hero = ({children, page}) => {
  const imgClassName = HeroItem.background[page || "hero"];
  const judulClassName = HeroItem.judul[page || "hero"];
  const contentClassName = HeroItem.content[page || "hero"];
  return (
    <div
      className="hero-wrapper"
      style={{
        backgroundImage: `url(${imgClassName})`,
      }}
    >
      <div className="hero">
        <div className="container">
          <div className="col-6">
          <h1>{judulClassName}</h1>
          <p>{contentClassName}</p>
          <div className="hero-btn">
              {children}
          </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Hero;
